













import { Prop, Component, Vue } from "vue-property-decorator";
import { Report, Section } from "@/models/report";
import { ChartType, CategoryChartData } from "@/models/chart-data";

import { ExportSvg } from "./shared";

import "./charts.css";

@Component
export default class BarChart extends Vue {
  @Prop(Object) public readonly data!: CategoryChartData;
  @Prop(String) public readonly title!: string | undefined;

  get chartData() {
    if (!this.data) { return; }
    const data: CategoryChartData = this.data;

    const chartData = {
      animation: false,
      textStyle: {
        fontFamily: "Inter, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', sans-serif"
      },
      grid: {
        top: 30,
        left: 50,
        right: 30,
        bottom: 60
      },
      tooltip: {
        trigger: "axis",
        position: (pt: any) => [pt[0], "10%"]
      },
      xAxis: {
        type: "category",
        data: data.categories,
        name: data.attributes[0].friendlyName,
        nameLocation: "middle",
        nameGap: 35
      },
      yAxis: {
        type: "value",
        min: data.attributes[0].scaleMin,
        max: data.attributes[0].scaleMax
      },
      series: [{
        data: data.values,
        type: "bar",
        showAllSymbol: true
      }]
    };
    return chartData;
  }

  public exportChart() {
    ExportSvg(this.$refs.chart, "Chart.svg");
  }
}
